@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2"), url("../fonts/.woff") format("woff");
}
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2");
  src: url("../fonts/pt-mono-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-medium.woff2") format("woff2");
  src: url("../fonts/roboto-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2");
  src: url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2");
  src: url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  overflow-x: hidden;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f2f5f6;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f2f5f6;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 8px;
  }
}

@media (max-width: 767px) {
  .accordion-body {
    overflow: hidden;
    height: 0;
  }
}

.accordion-section--opened .accordion-body {
  height: 100%;
}

@media (max-width: 767px) {
  .accordion-section {
    position: relative;
  }
}

@media (max-width: 767px) {
  .accordion-section--opened .footer__nav-button::before,
.accordion-section--opened .footer__contacts-button::before {
    display: none;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #f2f5f6;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(4, 24, 39, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 40px;
  background-color: #00b3ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__description {
  padding: 0;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 42px;
  color: #f2f5f6;
}

.modal__text {
  padding: 0;
  margin: 0;
  margin-bottom: 40px;
  font-size: 16px;
  line-height: 22px;
  color: #f2f5f6;
}

.modal__form input[type=text],
.modal__form input[type=tel],
.modal textarea {
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 22px;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
}

.modal textarea {
  margin: 0;
  padding-bottom: 0;
  padding-top: 25px;
  min-height: 155px;
  margin-bottom: 15px;
  color: #ffffff;
}

.modal textarea::placeholder {
  color: #ffffff;
}

.modal__form input::placeholder {
  color: #ffffff;
}

.modal__form button[type=submit] {
  padding: 15px 0;
  width: 100%;
  margin-bottom: 10px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #041427;
  border-radius: 3px;
}

.modal__form label:last-of-type {
  padding-left: 35px;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}

.modal__form input[type=checkbox] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 0;
  margin-bottom: 0;
}

.modal__form span {
  position: relative;
}
.modal__form span::before {
  position: absolute;
  content: "";
  top: 0;
  left: -25px;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
}

.modal__form input[type=checkbox]:checked ~ span::after {
  position: absolute;
  content: "";
  top: 5px;
  left: -20px;
  width: 7px;
  height: 6px;
  background-image: url("../img/svg/checkbox.svg");
}

.modal__form input[type=checkbox]:hover + span::before,
.modal__form input[type=checkbox]:focus + span::before {
  border: 1px solid #041427;
}

.modal__close-btn svg {
  fill: #ffffff;
}

span[data-error=""],
span[data-error-modal=""] {
  position: absolute;
  bottom: 15px;
  left: 58px;
  color: #ff1553;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
}
span[data-error=""]::before,
span[data-error-modal=""]::before {
  display: none;
}

form .error {
  outline: 1px solid #ff1553;
}

